import React, { useEffect, useState } from "react";
import HeaderComp from "../common/headerCommon";
import { Col, Row } from "react-grid-system";
import styled from "./style.module.css";
import { useDispatch, useSelector } from "react-redux";
import { GetImages } from "../../redux/store/images";
import ImagesPost from "./Post";
import ImagesDelete from "./Delete";
import ImagesPut from "./Put";

function ImagesProductsComp() {
  const disptach = useDispatch();
  useEffect(() => {
    disptach(GetImages());
  }, []);
  const Data = useSelector((state) => state.images.getImages.Data);
  console.log(Data);
  const [modal, setModal] = useState(false);
  const [delet, setDelete] = useState(false);
  const [put, setPut] = useState(false);
  const [del_id, setDel_id] = useState();
  const [put_id, setPut_id] = useState();

  const HandelOpen = () => {
    setModal(true);
  };
  const HandelClose = () => {
    setModal(false);
  };
  const HandelOpenDel = () => {
    setDelete(true);
  };
  const HandelCloseDel = () => {
    setDelete(false);
  };
  const HandelOpenPut = () => {
    setPut(true);
  };
  const HandelClosePut = () => {
    setPut(false);
  };
  return (
    <>
      <HeaderComp title="Products Images" handelOpen={HandelOpen} />
      <Row style={{ margin: 0, padding: 0, width: "100%" }}>
        {Data.map((elem, index) => (
          <Col key={index} xs={3} sm={3} className={styled.Col}>
            <img src={elem.image} alt="photo" />
            <h2 className={styled.text}>{elem.products.title}</h2>
            <div className={styled.buttons}>
              <button
                onClick={() => {
                  setDel_id(elem.id);
                  HandelOpenDel();
                }}
                className={styled.delete}
              >
                Delete
              </button>
              <button
                onClick={() => {
                  setPut_id(elem.id);
                  HandelOpenPut();
                }}
                className={styled.put}
              >
                Edit
              </button>
            </div>
          </Col>
        ))}
      </Row>
      <ImagesPost modal={modal} handleClose={HandelClose} />
      <ImagesDelete modal={delet} handleClose={HandelCloseDel} id={del_id} />
      <ImagesPut modal={put} handleClose={HandelClosePut} id={put_id} />
    </>
  );
}

export default ImagesProductsComp;
