import React, { useRef } from "react";
import { GlobalStyleLogin, Wrapper } from "./styled-index";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import "./styles.css";
import CommonButton from "../../components/common/button/index";
import logo from "../../assets/image/logos.png";
import { SingIn } from "../../redux/store/login";

function Login() {
  const EmailUserf = useRef();
  const dispatch = useDispatch();
  const passwordUsref = useRef();
  const admin = useSelector((state) => state);
  const HandleSubmit = async (e) => {
    e.preventDefault();
    const email = EmailUserf.current.value;
    const password = passwordUsref.current.value;
    await dispatch(SingIn({ email, password }));
  };
  return (
    <>
      <GlobalStyleLogin />
      <Wrapper>
        <div className="login-box">
          <div className="titles_box">
            <img src={logo} alt="" />
            <h2></h2>
          </div>
          <form onSubmit={HandleSubmit}>
            <div className="user-box">
              <input
                placeholder=". "
                type="email"
                name=""
                required
                ref={EmailUserf}
              />
              {/* <input ref={EmailUserf} type="email" name="" required /> */}
              {/* <label> </label> */}
            </div>
            <div className="user-box">
              <input
                ref={passwordUsref}
                placeholder=""
                type="password"
                name=""
                required
              />
            </div>
            {admin.admin?.Error == true ? (
              <p style={{ color: "red" }}>
                    
              </p>
            ) : null}
            <div className="btnss">
              <CommonButton type="submit"></CommonButton>
            </div>
          </form>
        </div>
      </Wrapper>
    </>
  );
}

export default Login;
